import React from 'react';
import Helmet from 'react-helmet';
import headerImage from "../images/header-image.jpg"

export function SEO(url:string) {
  return (
    <Helmet>
      <title>Mosaic: Find Hidden Artistic Connections with Deep Learning</title>
      <meta property="og:title" content="Mosaic: Find Hidden Artistic Connections with Deep Learning" />
      <meta property="og:image" content={headerImage}/>
      <meta property="og:url" content={url} />
      <meta property="og:description" content="Use deep learning to find hidden connections in art that span time, culture, and media." />
    </Helmet>    
  )
}
